// ToDo - add api routes

var mixins = {
  created() {},
  methods: {
    getPermissionLocale(permission) {
      switch (permission) {
        case "buy":
          return "";
        case "add":
          return "";
        case "show":
          return "";
        case "view":
          return " ";
        case "create":
          return "";
        case "edit":
          return "";
        case "delete":
          return "";
        case "moderate":
          return "";
        default:
          break;
      }
    },

    getRoleArray() {
      return {
        administrator: "",
        moderator: "",
        client: "",
        client_worker: ""
      };
    },

    getOrgStatusArray() {
      return {
        approve: "",
        archive: "",
        register: "",
        banned: ""
      };
    },

    getOrgStatus(status) {
      switch (status) {
        case "register":
          return "";
        case "approve":
          return "";
        case "archive":
          return "";
        case "banned":
          return "";
        default:
          break;
      }

      return "";
    },

    getOrgType(status) {
      switch (status) {
        case "seller":
          return "";
        case "buyer":
          return "";
        default:
          break;
      }

      return "";
    }
  }
};

export default mixins;



